.ContactData{
   margin: 20px auto;
   width: 80%;
   text-align: center;
   background-color: white;
   box-shadow: 0 2px 3px #ccc;
   border: 1px solid #eee;
   padding: 30px 50px;
   box-sizing: border-box;
}

.ContactData form{
   display:flex;
   flex-direction: column;
}

.ContactData button {
   margin-top: 40px;
}

@media (min-width:600px){
   .ContactData{
      width: 500px;
   }
}